* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.navbar {
  background-repeat: no-repeat;
  background-size: cover;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  width: 100%;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
  margin-right: 2em;
}

.container {
  z-index: 1;
  width: 100%;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: left;
  justify-content: left;
  list-style: none;
  text-align: left;
  justify-content: flex-start;
  margin-right: 2em;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-links {
  color: rgb(237, 9, 9);
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-weight: 800;
}

.nav-links:hover {
  color: #f4f6f7;
}

.fa-bars {
  color: #000;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 660px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    width: 100%;
  }

  .nav-menu.active {
    background: rgb(5, 5, 5);
    left: 0;
    opacity: 1;
    transition: all 0.3s ease;
    z-index: 1;
    flex-direction: column;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: rgb(172, 21, 21);
  }

  .nav-links:hover {
    color: aquamarine;
    transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
    height: 2em;
    width: 100%;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .avatar {
    margin: 1em;
    height: 3.2em;
    border-radius: 40px;
    width: 30%;
  }
}

.menu-icon svg {
  color: rgb(244, 12, 12) !important;
}

.avatar {
  margin: 1em;
  height: 3.2em;
  border-radius: 40px;
  width: 5em;
}

.avatar:hover {
  cursor: pointer;
}

.navbar ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-top: 1em;
  width: 100%;
}
